<template>
  <div class="col-md-3">
    <b>{{name}}</b> <button v-on:click="info">Info</button>
          <v-btn color="success">Success</v-btn>
  </div>
</template>

<script>
export default {
  data: function() {
    return {
      name: this.carrier.name,
      id: this.carrier.id
    }
  },
  props: ['carrier'],
  methods: {
    info() {
      console.log(JSON.stringify(this.name));
      this.$emit('myclick');
    }
  }
}
</script>

<style scoped>
  .b {
    font-weight: bolder;
  }
</style>



