<template>
  <v-app light>

    <v-container fluid class="pa-0">
      <v-layout row wrap>
        <v-flex md6 sm12>
          <div class="text-xs-center">
            <v-chip close>Example Chip</v-chip>
          </div>
          <div class="text-xs-center">
            <v-chip>Example Chip</v-chip>
          </div>
        </v-flex>
        <v-flex md6 sm12 xs12>
          <div class="text-xs-center">
            <v-chip close>
              <v-avatar>
                <img src="https://randomuser.me/api/portraits/men/35.jpg" alt="trevor">
              </v-avatar>
              Trevor Hansen
            </v-chip>
          </div>
          <div class="text-xs-center">
            <v-chip>
              <v-avatar class="teal">A</v-avatar>
              ANZ Bank
            </v-chip>
          </div>
        </v-flex>
      </v-layout>
    </v-container>

    <h3>Carriers</h3>
    <div class="row">
    <carrier v-for="(tab, index) in tabs"
                      :carrier="tab.carrier"
                      v-on:click="currentTab = tab"
                      v-on:myclick="clickedOn(tab, index)"
                      :key="index">
    </carrier>

    </div>

    <div>
      Plans from {{currentTab.carrier.name}}
    </div>
  </v-app>
</template>

<script>
export default {
  data: function() {
    return {
      currentTab: '',
      tabs: [{carrier: { name: 'Kaiser', id: 123}},
              {carrier: { name: 'UHC', id: 456}},
             {carrier: { name: 'BlueCross', id: 789}}]
    }
  },
  created: function () {
    this.currentTab = this.tabs[0];
    console.log('Loading data ...')
    // this.$http.get(this.$root.office_location_path).then(response => {
    //   console.log('Response: ' + response.body);
    //   this.rows = response.body;
    // }, response => {
    //   console.log('Error occurred trying to fetch office locations');
    // });
  },
  methods: {
      addRow(){
        this.rows.push({id: '', carrier: {}});
      },
      loadPlansFor(carrier_id){
        var self = this
        console.log('load plans for carrier id... ' + carrier_id)
        this.$http.get("/vue/" + carrier_id + "/load_plans")
        .then(response => {
          console.log(response.body);
        }, response => {
          console.error(response.body);
        });
      },
      save(){
        var self = this
        this.$http.post(this.$root.office_location_save_path, {office_locations: this.rows})
        .then(response => {
          console.log(response.body);
        }, response => {
          console.error(response.body);
        });
      },
      removeRow(index){
          this.rows.splice(index,1);
      },
      clickedOn(tab, index) {
        console.log('clicked on: ' + tab + ', ' + index);
        this.currentTab = tab;
        this.loadPlansFor(tab.carrier.id)
      }
  }
}
</script>



